import { useLocation } from "react-router-dom";
import Sidebar from "./Sidebar";
import axios from "axios";
import { useState, useEffect } from "react";

export default function UniversityRecommendations() {
  const location = useLocation();

  // Get the aggregate scores from location state
  const { scAggregate, hscAggregate, selectedSubjects } = location.state || {};

  const universities = [
    { name: "Polytechnics", minScore: 26, location: "USA" },
    { name: "Stanford University", minScore: 20, location: "USA" },
    { name: "MIT", minScore: 24, location: "USA" },
    { name: "University of Oxford", minScore: 25, location: "UK" },
    { name: "University of Cambridge", minScore: 18, location: "UK" },
    { name: "University of Toronto", minScore: 29, location: "Canada" },
    { name: "National University of Singapore", minScore: 26, location: "Singapore" },
    { name: "University of Cape Town", minScore: 24, location: "South Africa" },
    { name: "Local Community College", minScore: 10, location: "Your Country" },
  ];

  const [user, setUser] = useState({ _id: "12345" }); // Dummy user data, replace with actual auth context
  const [message, setMessage] = useState("");

  // Filter universities based on SC and HSC aggregates
  const eligibleUniversities = universities.filter(
    (uni) => scAggregate <= uni.minScore || hscAggregate <= uni.minScore
  );

  // Save user data to MongoDB
  const saveUserData = async () => {
    try {
      if (!user) {
        console.log("You need to sign up or log in to save your information.");
        return;
      }

      await axios.post("http://localhost:4000/save-profile", {
        userId: user._id,
        scAggregate,
        hscAggregate,
        selectedSubjects,
        recommendations: eligibleUniversities,
      });

      setMessage("Profile and university recommendations saved successfully!");
    } catch (error) {
      console.error("Error saving user data:", error);
    }
  };

  return (
    <div className="university-container">
      <Sidebar />
      <h3 className="university-header">Recommended Universities</h3>

      <div className="aggregate-display">
        <p><strong>SC Aggregate Score: </strong>{scAggregate ? scAggregate : 'Not Available'}</p>
        <p><strong>HSC Aggregate Score: </strong>{hscAggregate ? hscAggregate : 'Not Available'}</p>
      </div>

      <button className="save-button" onClick={saveUserData}>
        Save My Information
      </button>

      {message && <p className="success-message">{message}</p>}

      {eligibleUniversities.length > 0 ? (
        <div className="university-grid">
          {eligibleUniversities.map((uni, index) => (
            <div key={index} className="university-card">
              <img
                src={`/images/${uni.name.replace(/\s+/g, "").toLowerCase()}.jpg`}
                alt={uni.name}
                className="university-image"
              />
              <div className="university-info">
                <h4>{uni.name}</h4>
                <p>📍 {uni.location}</p>
                <p>🎓 Minimum Score: {uni.minScore}</p>
              </div>
              <button className="details-button">Learn More ➝</button>
            </div>
          ))}
        </div>
      ) : (
        <p className="no-match">No universities match this score.</p>
      )}
    </div>
  );
}
