import React, { useState } from "react";

const questions = [
    { text: "I love social interaction and get energized when meeting new people." },
    { text: "In a group setting, I tend to take the lead and keep the conversations going." },
    { text: "I feel comfortable starting conversations with strangers." },
    { text: "I enjoy being the center of attention in social settings." },
];

const options = ["Strongly Disagree", "Disagree", "Neutral", "Agree", "Strongly Agree"];

const accessment = {
    alignContent: "center",
    fontSize: "30px",
    fontWeight: "bold",
    marginBottom: "20px",
    margin: "10px",
    marginLeft: "auto",
    marginRight: "auto",
    border: "1px solid black",
    top: "10%",
    padding: "10px",
    borderRadius: "10px"
};

const Next = {
    borderRadius: "8px",
    padding: "5px",
    backgroundColor: "rgb(159, 245, 130)",
    border: "none",
    marginTop: "20px",
    width: "100px",
};

const Back = {
    marginLeft: "10px",
    borderRadius: "8px",
    padding: "5px",
    backgroundColor: "rgb(159, 245, 130)",
    border: "none",
    marginTop: "20px",
    width: "100px",
};

function PersonalityAssessment() {
    const [currentStep, setCurrentStep] = useState(0);
    const [responses, setResponses] = useState(Array(questions.length).fill(null));
    const [loading, setLoading] = useState(false);
    const [showResults, setShowResults] = useState(false);
    const [feedback, setFeedback] = useState("");

    const handleSelect = (index) => {
        const newResponses = [...responses];
        newResponses[currentStep] = index;
        setResponses(newResponses);
    };

    const generateFeedback = (questionIndex, answer) => {
        let responseFeedback = '';



        switch (questionIndex) {
            case 0:
                responseFeedback = answer === 4 ? "You're a social butterfly, full of energy!" :
                                   answer === 3 ? "You enjoy socializing but might need some personal time." :
                                   answer === 2 ? "You prefer avoiding social interactions." :
                                   answer === 0 ? "You’re more introverted and value solitude." : "";
                
                break;
            case 1:
                responseFeedback = answer === 4 ? "You're a natural leader in group settings!" :
                                   answer === 3 ? "You contribute but prefer others to take the lead." :
                                   answer === 2 ? "You tend to stay quiet and avoid taking the lead." :
                                   "You're most comfortable staying in the background during group settings.";
                break;
            case 2:
                responseFeedback = answer === 4 ? "You confidently initiate conversations!" :
                                   answer === 3 ? "You're comfortable with strangers but may not always start the conversation." :
                                   answer === 2 ? "You tend to avoid initiating conversations with strangers." :
                                   "You feel very uncomfortable approaching new people.";
                break;
            case 3:
                responseFeedback = answer === 4 ? "You thrive in the spotlight and enjoy being noticed!" :
                                   answer === 3 ? "You're comfortable being in the center of attention, but it's not your top priority." :
                                   answer === 2 ? "You prefer staying out of the spotlight." :
                                   "You avoid being the center of attention at all costs.";
                break;
            default:
                responseFeedback = "Thank you for your response!";
                break;
        }
        return responseFeedback;
    };

    const generateAllFeedback = () => {
        let allFeedback = responses.map((response, index) => generateFeedback(index, response));
        setFeedback(allFeedback.join(" "));
    };

    const handleNext = () => {
        if (currentStep < questions.length - 1) {
            setCurrentStep(currentStep + 1);
        } else {
            setLoading(true); // Show loader
            setTimeout(() => {
                generateAllFeedback(); // Generate feedback after all answers
                setShowResults(true);
                setLoading(false);
            }, 2000); // Simulate 2-second loading
        }
    };

    const handleRestart = () => {
        setCurrentStep(0);
        setResponses(Array(questions.length).fill(null));
        setShowResults(false);
        setLoading(false);
        setFeedback("");
    };

    const handleBack = () => {
        if (currentStep > 0) {
            setCurrentStep(currentStep - 1);
        }
    };

    return (
        <div className="Assess-container">

            <div className="progressbarcontainer">
                <div className="progress-bar progress2" style={{ width: `${(currentStep / questions.length) * 100}%` }}></div>
            </div>

            {!showResults ? (
                <div className="Assess-block">
                    <h2 className="Step"> {currentStep + 1}/{questions.length}</h2>
                    <p className="Questions">{questions[currentStep].text}</p>
                    <div className="Assess-btn">
                        {options.map((option, index) => (
                            <button
                                key={index}
                                id={`option-${index}`}
                                data-option={option}
                                onClick={() => handleSelect(index)}
                                className={`option-button ${responses[currentStep] === index ? "selected" : ""}`}
                            >
                                {option}
                            </button>
                        ))}
                    </div>
                    <div className="nextback">
                        <button
                            onClick={handleNext}
                            disabled={responses[currentStep] === null || loading}
                            style={Next}
                            id="Next"
                        >
                            {loading ? "Loading..." : (currentStep < questions.length - 1 ? "Next" : "Finish")}
                        </button>
                        <button onClick={handleBack} className="nav-btn back-btn" style={Back}>
                            Back
                        </button>
                    </div>
                </div>
            ) : (
                <div className="completion-screen">
                    <h2 style={accessment}>Assessment Complete!</h2>
                    <ul className="responses">
                        {responses.map((response, index) => (
                            <li key={index}>
                                <strong>Question {index + 1}:</strong>
                                <span> {options[response]}</span>
                            </li>
                        ))}
                    </ul>
                    <p className="feedback">{feedback}</p>
                </div>
            )}

            <div>
                <button onClick={handleRestart} className="restart-btn">
                    Restart Assessment
                </button>
            </div>

            {loading && (
                <div className="loader-container">
                    <div className="spinner"></div>
                    <p>Processing your results...</p>
                </div>
            )}
        </div>
    );
}

export default PersonalityAssessment;
